<template>
  <div>
    <div class="filter-container">
      <el-row :gutter="10">
        <el-col :sm="24" :md="12">
          <el-button class="filter-item" @click="handleCreate" type="primary" icon="el-icon-plus">{{$t('table.add')}}</el-button>
          <el-button class="filter-item" type="danger" :disabled="multipleSelection.length <= 0"
            v-if="multipleSelection.length>0"
            @click="handleDelete(multipleSelection)"
            icon="el-icon-delete">{{$t('table.delete')}} ({{multipleSelection.length}} item)</el-button>
        </el-col>
        <el-col :sm="24" :md="12">
          <el-input placeholder="Search" v-model="urlParams.s" class="input-with-select" @keyup.enter.native="getSuppliers">
            <el-button slot="append" icon="el-icon-search" @click="getSuppliers"></el-button>
          </el-input>
        </el-col>
      </el-row>
    </div>

    <div class="table-container">
      <el-table :data="suppliers" v-loading="loading" element-loading-text="Memuat data..." @selection-change="handleSelectionChange" border fit highlight-current-row style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label="Name" width="180" sortable>
        </el-table-column>

        <el-table-column prop="address" label="Address"  sortable>
        </el-table-column>

        <el-table-column prop="phone" label="Phone" width="180" sortable>
        </el-table-column>

        <el-table-column align="center" :label="$t('table.actions')" width="230" class-name="small-padding fixed-width">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="handleUpdate(scope.row)" icon="el-icon-edit">{{$t('table.edit')}}</el-button>
            <el-button type="danger" size="mini" style="width:80px;" @click="handleDelete([scope.row])" icon="el-icon-delete">{{$t('table.delete')}}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- Pagination -->
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pagination.currentPage"
          :page-sizes="pagination.sizes"
          :page-size="pagination.pageSize"
          layout="sizes, total, prev, pager, next"
          :total="pagination.total">
        </el-pagination>
      </div>
    </div>

    <el-dialog :title="textMap[formDialog.status]" :visible.sync="formDialog.visible" :width="formDialog.dialog.width">
      <el-form :model="supplierForm" :rules="supplierFormRules" ref="supplierForm" :label-position="formDialog.dialog.labelPosition" label-width="100px">
        <el-form-item label="Name" prop="name">
          <el-input type="text" v-model="supplierForm.name" placeholder="name"></el-input>
        </el-form-item>

        <el-form-item label="Address" prop="address">
          <el-input type="text" v-model="supplierForm.address" placeholder="address"></el-input>
        </el-form-item>

        <el-form-item label="Phone" prop="phone">
          <el-input type="text" v-model="supplierForm.phone" placeholder="phone"></el-input>
        </el-form-item>

        <!-- Add Products -->
        <div class="sub-title">
          <h3>Products</h3>
        </div>

        <div class="form-limited">
          <el-form-item
            v-for="(product, index) in supplierForm.products"
            :label="'Product ' + (index+1)"
            :key="'product-'+index"
            :prop="'products.' + index + '.product'"
            >
            <el-row :gutter="5">
              <el-col :sm="8">
                <el-select
                  style="width:100%"
                  v-model="product.product_id"
                  filterable
                  remote
                  placeholder="Search product"
                  :remote-method="getProducts"
                  :loading="loading">
                  <el-option
                    v-for="item in products"
                    :key="item.product_id"
                    :label="item.name"
                    :value="item.product_id">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :sm="6">
                <el-input type="number" v-model.number="product.selling_price" min="0" placeholder="selling price"></el-input>
              </el-col>
              <el-col :sm="2">
                <el-button type="danger" icon="el-icon-delete" @click.prevent="removeProduct(product)"></el-button>
              </el-col>
            </el-row>
          </el-form-item>
        </div>

        <el-form-item>
          <el-button style="margin-left:10px" size="mini" type="primary" icon="el-icon-plus" @click="addNewProduct">Add Product</el-button>
        </el-form-item>

        <el-form-item>
          <el-button @click="formDialog.visible = false">{{$t('table.cancel')}}</el-button>
          <el-button v-if="formDialog.status==='create'" :loading="loading" type="primary" @click="createData">{{$t('table.confirm')}}</el-button>
          <el-button v-else type="primary" @click="updateData">{{$t('table.confirm')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <app-delete-confirmation
      :visible="deleteDialog.visible"
      :on-cancel="handleCloseDeleteDialog"
      :on-delete="deleteData"
    ></app-delete-confirmation>

  </div>
</template>

<script>
import { dialog } from "@/responsive";
import deleteConfirmation from "@/components/dialog/DeleteConfirmation";
import { arrayToObject } from "@/utils/arrayToObject";

const formFields = [
  "name",
  "address",
  "phone",
  ["products", []]
];

export default {
  components: {
    appDeleteConfirmation: deleteConfirmation
  },
  data() {
    return {
      suppliers: [],
      products: [],
      loading: false,
      textMap: {
        update: "Edit",
        create: "Create"
      },

      // form
      formFields,
      supplierForm: arrayToObject(formFields),
      supplierFormRules: {
        name: [{ required: true }],
        address: [{ required: true }],
        phone: [{ required: true }]
      },

      // Add, edit dialog
      formDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md
      },

      // delete dialog
      deleteDialog: {
        visible: false,
        dialog: dialog.md30
      },
      deleteSupplierIds: [],

      // table
      multipleSelection: [],

      // Pagination
      pagination: {
        total: 0,
        pageSize: 10,
        currentPage: 1,
        sizes: [5, 10, 20, 30, 40, 50, 100]
      },

      // Get
      urlParams: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (supplier_id|name|created_at)
        sort: "desc"
      },

      // Responsive
      windowWidth: window.innerWidth
    };
  },

  methods: {
    // Create
    handleCreate() {
      this.resetForm();
      this.formDialog = {
        ...this.formDialog,
        status: "create",
        visible: true
      };
      this.$nextTick(() => {
        this.$refs["supplierForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    createData() {
      this.$refs.supplierForm.validate(valid => {
        if (valid) {
          this.loading = true;

          this.$store
            .dispatch("AddSupplier", this.supplierForm)
            .then(res => {
              this.getSuppliers();

              this.$message.success(res.data.messages[0]);
              this.formDialog.visible = false;
              this.loading = false;
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Update
    handleUpdate(row) {
      this.supplierForm = {
        name: row.name,
        address: row.address,
        phone: row.phone,
        products: row.products
      };
      this.formDialog = {
        status: "update",
        visible: true,
        selectedId: row.supplier_id
      };
      this.$nextTick(() => {
        this.$refs["supplierForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    updateData() {
      this.$refs.supplierForm.validate(valid => {
        if (valid) {
          this.loading = true;

          console.log(this.supplierForm.products);

          // add supplier Id
          this.supplierForm.supplierId = this.formDialog.selectedId;

          this.$store
            .dispatch("UpdateSupplier", this.supplierForm)
            .then(res => {
              this.getSuppliers();

              this.loading = false;
              this.formDialog.visible = false;
              this.$message.success(res.data.messages[0]);
              // this.$notify({
              //   title: "Success",
              //   message: res.data.messages[0],
              //   type: "success"
              // });
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Delete
    handleDelete(suppliers) {
      this.deleteDialog.visible = true;
      this.deleteSupplierIds = suppliers.map(supplier => {
        return supplier.supplier_id;
      });

      // Responsive
      this.makeDialogResponsive(dialog.sm, dialog.md30);
    },
    deleteData() {
      this.deleteDialog.visible = false;
      this.$store
        .dispatch("DeleteSupplier", this.deleteSupplierIds)
        .then(res => {
          // refetch suppliers data
          this.getSuppliers();

          this.$message.success(res.data.messages[0]);
          this.deleteDialog.visible = false;
          this.deleteSupplierIds = [];
        })
        .catch(error => {
          const errorMsg = error.response.data.messages;
          errorMsg.forEach(msg => {
            this.$message.error(msg);
          });
        });
    },
    handleCloseDeleteDialog(done) {
      this.deleteDialog.visible = false;
    },

    // Form
    resetForm() {
      this.supplierForm = arrayToObject(this.formFields);
    },
    addNewProduct() {
      this.supplierForm.products.push({
        product_id: null,
        selling_price: null
      });
    },
    removeProduct(item) {
      var index = this.supplierForm.products.indexOf(item);
      if (index !== -1) {
        this.supplierForm.products.splice(index, 1);
      }
    },

    // Table
    handleSelectionChange(suppliers) {
      this.multipleSelection = suppliers;
    },

    // Pagination
    handleSizeChange(pageSize) {
      this.pagination = {
        ...this.pagination,
        pageSize
      };
      this.urlParams = {
        ...this.urlParams,
        l: pageSize
      };

      this.getSuppliers();
    },
    handleCurrentChange(currentPage) {
      this.pagination = {
        ...this.pagination,
        currentPage
      };
      this.urlParams = {
        ...this.urlParams,
        p: currentPage
      };
      this.getSuppliers();
    },

    // Get
    getSuppliers() {
      this.loading = true;
      this.$store.dispatch("FetchSuppliers", this.urlParams).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.suppliers = data.data.suppliers;
        this.loading = false;

        // set pagination
        this.pagination = {
          ...this.pagination,
          total: data.data.total
        };
      });
    },
    getProducts(query) {
      const urlParams = {
        s: query,
        l: 10,
        p: 1,
        oby: "created_at", // order_by (supplier_id|name|created_at)
        sort: "desc"
      };

      this.loading = true;
      this.$store.dispatch("FetchProducts", urlParams).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.products = data.data.products;
        this.loading = false;
      });
    },

    // Responsive
    makeDialogResponsive(sm = dialog.sm, md = dialog.md) {
      if (this.windowWidth < 990) {
        this.formDialog = {
          ...this.formDialog,
          dialog: sm
        };
      } else {
        this.formDialog = {
          ...this.formDialog,
          dialog: md
        };
      }
    }
  },

  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    }
  },

  created() {
    this.getSuppliers();
  },

  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener("resize", function(e) {
        that.windowWidth = window.innerWidth;
      });
    });
  }
};
</script>

<style scoped>

</style>



